/* General container */
.container {
  text-align: center;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
  max-width: 900px;
  margin: 0 auto;
}

/* Page title */
h1 {
  color: #333;
  margin-bottom: 10px;
}

/* Status message */
.status {
  font-size: 16px;
  margin-bottom: 20px;
  color: #555;
}

/* Buttons */
button {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  transition: 0.3s;
}

/* Start chat button */
.start-btn {
  padding: 12px 25px;
  background-color: #28a745;
  color: white;
  margin-bottom: 20px;
}

.start-btn:hover {
  background-color: #218838;
}

/* Skip button */
.skip-btn {
  padding: 10px 20px;
  background-color: #ff8c00;
  color: white;
  margin-bottom: 20px;
}

.skip-btn:hover {
  background-color: #e67600;
}

/* Video container */
.video-container {
  display: flex;
  justify-content: center;
  margin: 20px 0;
  flex-wrap: wrap;
  gap: 20px;
}

/* Video elements */
.video {
  width: 45%;
  max-width: 400px;
  border: 2px solid #333;
  border-radius: 8px;
  background-color: black;
}

/* Chat container */
.chat-container {
  width: 100%;
  max-width: 600px;
  margin: 0 auto 30px auto;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 10px;
  background-color: #f9f9f9;
}

/* Messages area */
.messages {
  height: 200px;
  overflow-y: auto;
  padding: 10px;
  background-color: #fff;
  border-radius: 5px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
}

/* Individual message styles */
.messages p {
  margin: 5px 0;
  padding: 5px 8px;
  border-radius: 5px;
  max-width: 80%;
  word-wrap: break-word;
}

/* User messages */
.me {
  background-color: #d0ebff;
  color: #004085;
  margin-left: auto;
  text-align: right;
}

/* Stranger messages */
.stranger {
  background-color: #f8d7da;
  color: #721c24;
  margin-right: auto;
  text-align: left;
}

/* Input box */
.input-box {
  display: flex;
  gap: 10px;
}

/* Input field */
input[type="text"] {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

/* Send button */
.input-box button {
  padding: 10px 15px;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
}

.input-box button:hover {
  background-color: #0069d9;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .video {
    width: 90%;
  }

  .chat-container {
    width: 95%;
  }
}
